@import "https://fonts.googleapis.com/css?family=Bungee";
* {
  touch-action: manipulation;
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  font-family: sans-serif;
  font-size: 16px;
}

p {
  margin-bottom: 10px;
}

.container {
  background-color: #f5f5f5;
  border: 5px solid #000;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: 1fr;
  place-content: start;
  gap: 30px 20px;
  margin: 12px 6px;
  display: grid;
}

.container--confidence {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  gap: 10px;
  margin-bottom: 5px;
  display: grid;
  position: absolute;
  bottom: 5vh;
  transform: translate(5px);
}

.title--language {
  text-align: center;
  align-self: center;
  margin-top: 20px;
}

.el {
  min-width: 0;
  font-family: sans-serif;
  font-size: 16px;
}

.experimenters {
  margin-top: 40px;
}

.instruction-title {
  text-align: center;
  margin-top: 12px;
  font-family: Bungee, sans-serif;
  font-size: 30px;
  font-weight: bold;
}

.el--1 {
  grid-row: 1 / 2;
  align-self: center;
  margin: 0% 2%;
}

.el--2 {
  grid-row: 2 / 4;
  align-self: center;
  margin: 0% 2%;
}

.el--3 {
  grid-row: 4 / 5;
  justify-content: center;
  gap: 50px;
  padding: 50px;
  display: flex;
}

.el--4 {
  justify-content: center;
  gap: 50px;
  margin-bottom: 10px;
  padding: 0;
  position: absolute;
  bottom: 0;
  left: 23%;
}

.btn {
  text-align: center;
  background-color: #b3b3b3;
  border: none;
  border-radius: 4px;
  flex-grow: 0;
  width: 300px;
  padding: 10px;
  font-family: Bungee, sans-serif;
  font-size: 20px;
}

.btn--survey {
  text-align: center;
  color: #fff;
  background-color: #15803d;
  width: 200px;
  height: 50px;
  display: inline-block;
}

.btn--survey:link, .btn--survey:visited {
  color: #fff;
  text-decoration: none;
}

.btn:active {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 3px solid #15803d;
  transform: scale(.95, .85);
}

.lan {
  text-align: center;
  justify-content: space-between;
  place-self: center;
  align-items: center;
  gap: 10px;
  width: 250px;
  height: 100px;
  display: flex;
}

#frImg {
  width: 50px;
  height: 50px;
}

#languageEng {
  grid-row: 2 / 3;
}

#languageFr {
  grid-row: 3 / 4;
}

.close-modal {
  color: #333;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 3rem;
  position: absolute;
  right: 7rem;
}

.overlay {
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #0009;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.modal {
  z-index: 10;
  background-color: #f5f5f5;
  border-radius: 5px;
  grid-template-rows: .2fr 2fr 2fr 1fr;
  row-gap: 10px;
  width: 80%;
  height: 100%;
  padding: 1rem 6rem;
  display: grid;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -40%);
  box-shadow: 0 3rem 5rem #0000004d;
}

