  text-align: center;
  grid-row: 4 / 5;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  gap: 10px 50px;
  margin-top: 100px;
  margin-left: 15px;
  margin-right: 15px;
  font-size: 15px;
  display: grid;
}

.btn--consent {
  transform: translate(20px, 10px);
}

.btn--consentChoice {
  color: #fff;
  border: none;
